import React, { useState } from "react";

const Buttons = () => {
    const [display, setDisplay] = useState('');

    const handleClick =(number) =>{
        setDisplay(display+number);
    }

    const clearDisplay = () =>{
        setDisplay('');
    }
    const delLastCharacter = () => {
        setDisplay(display.slice(0, -1));
      }
    const valuateExpression = () =>{
        setDisplay(eval(display));
    }
    const calculatePercentage = () => {
        try {
          // Evaluate the expression in the display
          const result = eval(display);
    
          if (!isNaN(result)) {
            // Calculate the percentage (result * 100 to get the percentage)
            const percentageValue = (result * 100).toString();
            setDisplay(percentageValue);
          } else {
            // Handle invalid input or division by zero
            setDisplay('Error');
          }
        } catch (error) {
          // Handle any evaluation errors
          setDisplay('Error');
        }
      }
      
      
    return(
        <div className="button-body">
            <h1 className="title">Calculator</h1>
            <div className="buttons">
                <input type="text" className="display" value={display} placeholder="0"/>
                <button className="operator" value='C' onClick={clearDisplay}>C</button>
                <button className="operator" value='Del' onClick={delLastCharacter}>Del</button>
                <button className="operator" value='C'onClick={() => handleClick('+')}>+</button>
                <button className="operator" value='-'onClick={() => handleClick('-')}>-</button>
                <button className="number" onClick={() => handleClick('7')}>7</button>
                <button className="number" value='7'onClick={() => handleClick('8')}>8</button>
                <button className="number" value='7' onClick={() =>handleClick('9')}>9</button>
                <button className="operator" value='*'onClick={() =>handleClick('*')}>*</button>
                <button className="number" value='4' onClick={()=>handleClick('4')}>4</button>
                <button className="number" value='5'onClick={()=>handleClick('5')}>5</button>
                <button className="number" value='6' onClick={()=>handleClick('6')}>6</button>
                <button className="operator" value='/'onClick={() =>handleClick('/')}>/</button>
                <button className="number" value='1'onClick={()=>handleClick('1')}>1</button>
                <button className="number" value='2'onClick={()=>handleClick('2')}>2</button>
                <button className="number" value='3'onClick={()=>handleClick('3')}>3</button>
                <button className="operator" value='%' onClick={calculatePercentage}>%</button>
                <button className="number" value='0'onClick={()=>handleClick('0')}>0</button>
                <button className="number" value='00'onClick={()=>handleClick('00')}>00</button>
                <button className="number" value='.'onClick={()=>handleClick('.')}>.</button>
                <button className="equals" value = '=' onClick={valuateExpression}>=</button>
            </div>
        </div>
    );
}

export default Buttons;